<template>
  <v-app id="inspire">
    <v-main class="blue-grey lighten-1">
      <v-container style="max-width: 450px" fill-height>
        <v-layout align-center row wrap>
          <v-flex xs12>
            <v-card
            :loading = "loading">
              <div class="pa-5">
                <h1 style="text-align: center" class="mb-5">Sign up</h1>
                <v-form ref="form" lazy-validation>
                  <v-text-field
                      :rules="idrule"
                      :error-messages="id_dup"
                      label ="ID"
                      v-model="uid"
                      prepend-icon="mdi-account-alert"
                      required
                      >

                  </v-text-field>

                  <v-text-field
                      :rules="pw_rule"
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show1 ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      label="password"
                      v-model="pw"
                      hint="최소 8글자 이상이어야 합니다."
                      @click:append="show1 = !show1"
                  >
                  </v-text-field>
                  <v-text-field
                      :rules="pwcheck_rule"
                      :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show2 ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      label="Re-enter password"
                      v-model="validatepassword"
                      @click:append="show2 =! show2"
                  >

                  </v-text-field>
                  <v-text-field
                      :rules="unamerule"
                      prepend-icon="mdi-card-account-details"
                      type="text"
                      label="Name"
                      v-model="uname"
                  >
                  </v-text-field>
                  <v-text-field
                      v-model="email"
                      :rules="emailrule"
                      :error-messages="email_dup"
                      :append-outer-icon="email ? 'mdi-send' : none"
                      prepend-icon="mdi-email-check"
                      type="text"
                      label="Email"
                      @click:append-outer="sendEmail"
                  >
                    <v-snackbar
                        dense outlined
                        dismissible
                        type="warning"
                        v-model="email_alert"
                        timeout="3000"
                        color="red accent-1"
                    >
                      인증번호를 발송하였습니다.!
                    </v-snackbar>
                  <v-snackbar
                      dense outlined
                      dismissible
                      type="warning"
                      v-model="alert"
                      timeout="3000"
                      color="blue lighten-1"
                  >
                    내용을 다시 확인해 보세요.
                  </v-snackbar>

                  </v-text-field>

                  <v-snackbar
                      dense outlined
                      dismissible
                      type="warning"
                      v-model="signalert"
                      timeout="3000"
                      color="red lighten-1"
                  >
                    서버통신 에러.
                  </v-snackbar>

                  <v-btn
                      type="button"
                      color="purple lighten-3 text-capitalize"
                      depressed
                      large
                      block
                      dark
                      class="mb-3"
                      @click="submit"
                  >
                    Submit
                  </v-btn>

                </v-form>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

export default {
  data () {
    return{
      uid : '',
      loading: false,
      alert : false,
      signalert : false,
      id_dup: [],
      idrule : [
          v => !!v || '아이디는 필수 입력사항입니다.',
          v => /^[a-zA-z0-9]*$/.test(v) || '아이디는 영문 + 숫자만 입력 가능합니다.',
          v => !(v && v.length >= 15) || '아이디는 15자 이상 입력할 수 없습니다.',
          v => {
            this.idcheck(v)
          }
      ],
      pw : '',
      show1 : false,
      pw_rule:[
          v => !!v || '패스워드는 필수 입력사항입니다.',
          v => !(v && v.length >= 30) || '패스워드는 30자를 넘을 수 없습니다.',
          v => !(v && v.length < 8 ) || '패스워드는 8자를 넘어야합니다.',
      ],
      pwvalue : '',
      validatepassword : '',
      show2 : false,
      pwcheck_rule : [
        v => !!v || '패스워드는 필수 입력사항입니다.',
        v => !(v && v.length >= 30) || '패스워드는 30자를 넘을 수 없습니다.',
        v => v === this.pw || '패스워드가 일치하지 않습니다.',

      ],
      uname: '',
      unamerule : [
          v => !!v || '이름은 필수 입력사항입니다.',
          v => !(v && v.length >=20) || '이름은 20자 이상 입력할 수 없습니다.',
          v => !/[0-9]/.test(v) || '이름에는 숫자를 입력할 수 없습니다.',
          v => !/[~!@#$%^&*()_+|<>?:{}]/.test(v) || '이름에는 특수문자를 사용할 수 없습니다.'
      ],
      email: '',
      email_dup:'',
      emailrule : [
          v => !!v || '이메일은 필수입니다.',
          v => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'email양식을 확인해주세요.'
        ],
      email_alert: false,
    }
  },
  methods: {
    submit(){
      //모든값이 제대로 들어갔는지?
      let validate = this.$refs.form.validate();
      if (this.id_dup.length === 1)
        validate = false;
      if(validate){
        this.alert = false;
        this.signup()
      }
      else{
        this.alert = true;
      }
    },
    signup(){
      this.loading = true
      setTimeout(()=> (this.loading = false),2000)
      this.$http.post("/auth/signup",{
        uid: this.uid,
        password: this.pw,
        uname: this.uname,
        email: this.email,
      })
          .then(res =>{
            console.log(res.data)
            if(res.data === this.uid){
              console.log(res.data + "signup")
              this.$router.push({name: "SignupComplete"})
            }else{
              this.signalert = true
            }
          })
          .catch(e =>{
            this.signalert=true
            console.log(e)
          });

    },
    async idcheck(v){
      await this.$http.post("/auth/checkid",{
        uid : v,
      })
      .then(res =>{
        //아이디 중복
        let data = String(res.data)

        if(data === "true") {
          this.id_dup = ['아이디가 중복입니다.']
        }else{
          this.id_dup=[]
        }
      }).catch(e =>{
        console.log(e.message)
      })
    },
    async sendEmail(){
      await this.$http.post("/auth/emailcheck",{
        email : this.email
      })
      .then(res => {
        this.email_alert = true
        let data = String(res.data)
        if(data === "true"){
          this.email_dup = ['이메일이 유효하지 않습니다.']
        }else{
          this.email_dup=[]
        }
      }).catch(e => {
        console.log(e.message)
          })
    },


  },

  mounted() {
  },


}

</script>